html, body {
  font-family: "Segoe UI", sans-serif;
  color: #000000;
}

h1, h2, h3, h4, p {
  margin: 0;
}

#root {
  width: 100%;
  height: 100%;
}

input:focus {
  outline: 0;
  box-shadow: 0 0 3pt 2pt rgba(215, 215, 215, 0.4);
}

*::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

*::-webkit-scrollbar-track {
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.1);
}

*::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.4);
}

*::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.6);
}

*::-webkit-scrollbar-thumb:active {
  background: rgba(0, 0, 0, 0.9);
}

.text-orange {
  color: #ed6d1a !important;
  font-weight: bold;
}

.head-title-hero {
  text-align: center;
  font-size: 2.25em;
}

.sub-title-hero {
  margin-top: 1.250em;
  text-align: center;
  font-size: 1.125em;
}

.playlist-con-title {
  font-size: 1.5em;
  color: #000000;
  margin-bottom: 1em;
}

.playlist-con-sub-title {
  font-size: 1em;
  font-weight: 400;
  margin-bottom: 2.25em;
}

.playlist-con-title, .sub-title-hero, .sub-title-hero {
  font-weight: 500;
  text-align: center;
}

.head-title-body {
  text-align: center;
  font-size: 2.25em;
  font-weight: bold;
  margin-top: 1.75em;
}

.sub-title-body {
  text-align: center;
  font-size: 1.875em;
  font-weight: 500;
  margin-top: 2.188em;
}

.sub-title-body.what {
  margin-top: 1em;
  margin-bottom: 0.8em;
}

@media screen and (max-width: 475px) and (min-width: 320px) {
  .head-title-hero {
    font-size: 1.75em;
  }
  .playlist-con-title {
    font-size: 1.2em;
  }
  .password-wrap .disclaimer-star {
    top: 10px !important;
    right: 18px !important;
  }
}

.btn {
  width: 200px;
  border: none;
  padding: 15px 0px;
  cursor: pointer;
  color: #c8c8c8;
}

.btn:hover {
  color: #ffffff;
}

.btn-default {
  background-color: #ed6d1a;
  font-weight: bold;
  border-radius: 10px;
}

.btn-download {
  width: 150px;
  background-color: #3c3c3c;
  font-weight: bold;
  border-radius: 10px;
}

.flame {
  height: 26px;
  width: 18px;
  background: url("/img/flame_img.png");
  display: block;
}

.hero-wrap {
  background: linear-gradient(to bottom, black 0%, rgba(0, 0, 0, 0.25) 34%, rgba(255, 255, 255, 0) 100%), url("/img/hero_img.jpg");
  background-position: center;
  background-size: cover;
  width: 100%;
}

.hero-container {
  width: 1200px;
  height: 75vh;
  margin: auto;
  color: #ffffff;
  display: flex;
  flex-direction: column;
}

.slogan-wrap {
  margin-top: 130px;
}

.logo-wrap {
  position: absolute;
  top: 20px;
}

.logo-wrap img {
  width: 165px;
}

.logo-wrap a {
  display: block;
}

.playlist-form-container {
  color: #000000;
  width: 475px;
  margin: auto;
  background-color: #ffffff;
  border-radius: 10px;
  padding: 2.188em;
  text-align: center;
}

.playlist-form {
  width: 390px;
  margin: auto;
}

.link-wrap, .password-wrap {
  position: relative;
  width: 100%;
  margin: auto;
}

.link-wrap .error-container, .password-wrap .error-container {
  width: 100%;
  text-align: center;
  color: #ed6d1a;
  font-weight: bold;
}

.link-wrap input, .password-wrap input {
  margin-left: 2px;
  padding: 10px 20px;
  border-radius: 10px;
  border: 1px solid #cccccc;
  background-color: #f1f1f1;
  text-align: center;
  width: 73%;
  font-weight: bold;
  display: inline-block;
  box-sizing: border-box;
}

.link-wrap label, .password-wrap label {
  width: 25%;
  font-size: 18px;
  font-weight: 500;
  display: inline-block;
}

.link-wrap {
  margin-bottom: 20px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}

.link-wrap .flame {
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: -20px;
  transform: translateY(-50%);
}

.password-wrap {
  margin-bottom: 30px;
  opacity: 0.3;
  transition: 0.2s opacity ease-in;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}

.password-wrap.active {
  opacity: 1;
}

.password-wrap:hover {
  opacity: 1;
}

.password-wrap input:focus .password-wrap {
  opacity: 1;
}

.password-wrap .disclaimer-star {
  position: absolute;
  top: 0;
  right: -15px;
}

.disclaimer {
  position: relative;
  margin-bottom: 1.563em;
  text-align: center;
}

.disclaimer .disclaimer-star {
  position: absolute;
  top: -5px;
  left: -10px;
}

.disclaimer p {
  font-size: 0.85em;
  color: #787878;
}

.start-button-wrap {
  text-align: center;
}

.disclaimer-star {
  color: #ed6d1a;
  font-weight: 500;
  font-size: 22px;
}

@media screen and (max-width: 1220px) and (min-width: 960px) {
  .hero-container {
    width: 960px;
  }
}

@media screen and (max-width: 960px) and (min-width: 768px) {
  .hero-container {
    width: 768px;
  }
}

@media screen and (max-width: 475px) and (min-width: 320px) {
  .hero-container {
    width: 100%;
    height: 85vh;
  }
  .logo-wrap {
    left: 5px;
  }
  .playlist-form-container {
    width: 70%;
  }
  .playlist-form {
    width: 95%;
  }
  .link-wrap, .password-wrap {
    flex-direction: column;
    width: 100%;
  }
  .link-wrap input, .password-wrap input {
    width: 70%;
  }
}

.playlist-hero-wrap {
  width: 100%;
  height: 40vh;
  background: url("/img/roompage-hero.jpg");
  background-position: center;
  background-size: cover;
}

.playlist-title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 575px;
  height: 100px;
  color: #ffffff;
  background-color: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.playlist-title h1 {
  font-size: 36px;
}

@media screen and (max-width: 1220px) and (min-width: 960px) {
  .playlist-container {
    width: 960px !important;
  }
}

@media screen and (max-width: 960px) and (min-width: 768px) {
  .playlist-container {
    width: 768px !important;
  }
}

@media screen and (max-width: 475px) and (min-width: 320px) {
  .playlist-container {
    width: 100% !important;
  }
}

.how-page-body {
  margin-bottom: 200px;
}

.lantern {
  font-weight: bold;
  padding: 1px 5px;
  background-color: #000000;
  color: #ffffff;
}

.explanation-container {
  padding: 0px 15px;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
}

.explanation-container.what {
  justify-content: center;
}

.explanation-container.how {
  margin-top: 5em;
  justify-content: space-between;
}

.explanation-box {
  text-align: center;
}

.explanation-box.how {
  width: 33%;
}

.explanation-box.how-max {
  width: 100%;
}

.explanation-box.what {
  width: 800px;
}

.explanation-box.what p {
  margin-top: 2em;
  color: #333333;
  font-weight: 400;
}

.explanation-box.what h2 {
  font-weight: 600;
  margin: 1em 0;
}

.explanation-box p {
  font-size: 1.125em;
  font-weight: 600;
  line-height: 1.5em;
}

.explanation-box .exp-img {
  width: 400px;
  height: 200px;
  display: block;
  margin: auto;
}

.explanation-box .exp-img.click-start {
  background: url("/img/sprite-sheet-main-page-exp.png") 800px 0px;
}

.explanation-box .exp-img.copy-paste {
  background: url("/img/sprite-sheet-main-page-exp.png") 1200px 0px;
}

.explanation-box .exp-img.drag-drop {
  background: url("/img/sprite-sheet-main-page-exp.png") 0px 0px;
}

.explanation-box .exp-img.projector {
  background: url("/img/sprite-sheet-main-page-exp.png") 400px 0px;
}

@media screen and (max-width: 1285px) and (min-width: 960px) {
  .explanation-box .how {
    width: 50%;
    margin-bottom: 1.75em;
  }
  .explanation-container {
    flex-wrap: wrap;
  }
}

@media screen and (max-width: 960px) and (min-width: 768px) {
  .explanation-container {
    flex-direction: column;
  }
  .explanation-box.how {
    width: 100%;
  }
}

.playlist-container {
  position: relative;
  width: 1200px;
  height: 100%;
  margin: auto;
}

.playlist-body {
  height: 60vh;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.password-container {
  box-sizing: border-box;
  width: 100%;
  height: calc(100% - 185px);
  padding: 10px;
}

.playlist-password-wrap {
  width: 100%;
  min-height: 200px;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
  box-sizing: border-box;
}

.playlist-password-wrap .btn {
  margin: 0 auto;
}

.playlist-password-wrap label {
  font-size: 18px;
  font-weight: 500;
}

.playlist-password-wrap input[type="password"] {
  box-sizing: border-box;
  width: 100%;
  border-radius: 10px;
  border: 1px solid #cccccc;
  background-color: #f1f1f1;
  text-align: center;
  padding: 10px 20px;
}

.explanation-password {
  text-align: center;
}

@keyframes pulse {
  0% {
    opacity: 0.7;
    color: #303030;
  }
  25% {
    opacity: 1;
    color: #ed6d1a;
  }
  50% {
    opacity: 1;
    color: #ed6d1a;
  }
  100% {
    opacity: 0.7;
    color: #303030;
  }
}

.playlist-dropzone-wrapper {
  display: flex;
  border-radius: 10px;
  box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.1), 0 10px 30px 0 rgba(0, 0, 0, 0.2);
  width: 70%;
  height: 90%;
}

.playlist-wrap {
  width: 100%;
  height: 100%;
}

.playlist-wrap .playlist-list {
  width: 100%;
  height: 100%;
  padding: 10px 0px;
  box-sizing: border-box;
}

.controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 10px;
}

.controls .download, .controls .play, .controls .options, .controls .confirm {
  text-decoration: none;
  display: block;
  color: #aaaaaa;
  padding: 10px;
  font-weight: bold;
  cursor: pointer;
}

.controls .download:hover, .controls .play:hover, .controls .options:hover, .controls .confirm:hover {
  color: #ffffff;
  background-color: #000000;
}

.controls .play, .controls .confirm {
  animation-name: pulse;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

.controls .duration input {
  border: 1px solid #cccccc;
  border-radius: 5px;
  padding: 5px;
  width: 25%;
}

.controls .duration, .controls .autoplay, .controls .confirm {
  box-sizing: border-box;
  width: 33%;
  text-align: center;
}

.list {
  width: 100%;
  height: 85%;
  list-style: none;
  padding: 5px;
  margin: 10px 0 0 0;
  overflow-y: scroll;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  box-sizing: border-box;
}

.list .playlist-item {
  width: 48%;
  margin-bottom: 0.2em;
}

.error-list {
  height: 85%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #cccccc;
}

.thumbnail {
  width: 100%;
  height: 200px;
  position: relative;
  overflow: hidden;
  background-color: #cccccc;
}

.thumbnail img {
  position: absolute;
  left: 50%;
  top: 50%;
  height: 100%;
  width: auto;
  transform: translate(-50%, -50%);
}

.thumbnail .overlay {
  z-index: 200;
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.39);
}

.thumbnail .overlay i {
  cursor: pointer;
  color: #ffffff;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.thumbnail .overlay:hover {
  opacity: 1;
}

.dropzone {
  overflow-y: scroll;
  margin: auto;
  min-width: 40%;
  height: 100%;
  background-color: #f1f1f1;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border: none;
  border-bottom: 1px solid #cccccc;
  color: #999999;
  padding: 10px;
}

.dropzone .dz-preview {
  margin: 10px;
}

.dropzone h1 {
  font-size: 1.125em;
}

.dropzone p {
  font-size: 1.750em;
  font-weight: bold;
}

.title-dropzone {
  display: flex;
  justify-content: center;
  align-items: center;
}

.title-dropzone span {
  margin-right: 0.2em;
}

.icon-dropzone {
  display: block;
  width: 70px;
  height: 70px;
  background: url("/img/plus-icon.png");
  margin-bottom: 20px;
}

.dropzone .dz-preview .dz-success-mark,
.dropzone .dz-preview .dz-error-mark {
  z-index: 201;
}

#slideShow {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 400;
}

#go-fullscreen, #exit-fullscreen, #quit-slideshow {
  cursor: pointer;
  color: #ffffff;
  position: absolute;
  z-index: 401;
  opacity: 0.5;
  transition: 0.2s ease-in opacity;
}

#go-fullscreen:hover, #exit-fullscreen:hover, #quit-slideshow:hover {
  opacity: 1;
}

#go-fullscreen, #exit-fullscreen {
  left: 10px;
  top: 10px;
}

#quit-slideshow {
  top: 10px;
  right: 10px;
}

#prev-file, #next-file {
  z-index: 401;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: #ffffff;
  opacity: 0.5;
  transition: 0.2s opacity ease-in;
  cursor: pointer;
}

#prev-file:hover, #next-file:hover {
  opacity: 1;
}

#prev-file {
  left: 10px;
}

#next-file {
  right: 10px;
}

.slide-show-list {
  margin: 0;
  width: 100%;
  height: 100%;
  position: relative;
  list-style: none;
  box-sizing: border-box;
  padding: 0;
}

.slide-show-item {
  position: absolute;
  opacity: 0;
  transition: 0.2s opacity ease-in;
  padding: 50px;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slide-show-item img {
  max-width: 80%;
}

.slide-show-item.active {
  opacity: 1;
}

@media screen and (max-height: 730px) {
  .playlist-body {
    flex-direction: row;
  }
}

.footer {
  width: 100%;
  height: 50px;
  background-color: #eeeeee;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer .flame {
  margin-right: 5px;
}

.footer p {
  color: #afafaf;
}
